.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.modal-form {
  width: 450px;
  height: 450px;
  background-color: #fff;
  border-radius: 4px;
  padding: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.contact-form {
  flex-direction: column;
  display: flex;
}

.contact-form-input {
  height: 30px;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  margin-top: 5px;
  margin-bottom: 10px;
  padding-left: 10px;
}

